'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { Eye, EyeOff, User, Mail, Shield, FileUser } from 'lucide-react';

export function RegisterForm() {
  const router = useRouter();
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    nombre: '',
    apellido: '',
    password: '',
    confirmPassword: '',
  });
  const [errors, setErrors] = useState<Record<string, string>>({});
  const [isLoading, setIsLoading] = useState(false);
  const [success, setSuccess] = useState(false);

  // Toggles para mostrar/ocultar contraseñas
  const [showPass, setShowPass] = useState(false);
  const [showConfirmPass, setShowConfirmPass] = useState(false);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));

    if (errors[name]) {
      setErrors(prev => {
        const next = { ...prev };
        delete next[name];
        return next;
      });
    }
  };

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.username) newErrors.username = 'El usuario es requerido';
    else if (formData.username.length < 3)
      newErrors.username = 'Debe tener al menos 3 caracteres';

    if (!formData.email) newErrors.email = 'El email es requerido';
    else if (!/\S+@\S+\.\S+/.test(formData.email))
      newErrors.email = 'Email inválido';

    if (!formData.nombre) newErrors.nombre = 'El nombre es requerido';

    if (!formData.apellido) newErrors.apellido = 'El apellido es requerido';

    if (!formData.password) newErrors.password = 'La contraseña es requerida';
    else if (formData.password.length < 6)
      newErrors.password = 'Debe tener al menos 6 caracteres';

    if (formData.password !== formData.confirmPassword)
      newErrors.confirmPassword = 'Las contraseñas no coinciden';

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsLoading(true);

    setTimeout(() => {
      const users = JSON.parse(localStorage.getItem('registered_users') || '[]');
      users.push({
        id: users.length + 1,
        ...formData,
        rol: 'vendedor',
        createdAt: new Date().toISOString(),
      });

      localStorage.setItem('registered_users', JSON.stringify(users));
      setSuccess(true);
      setIsLoading(false);

      setTimeout(() => router.push('/login'), 2000);
    }, 1000);
  };

  // ÉXITO → Mismo estilo moderno pero verde
  if (success) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-900 via-slate-800 to-black text-white px-4">
        <div className="rounded-3xl backdrop-blur-2xl bg-white/10 shadow-2xl p-10 max-w-md w-full text-center border border-white/10 animate-fade-in">
          <div className="w-24 h-24 rounded-full bg-green-500/20 mx-auto mb-5 flex items-center justify-center">
            <svg className="w-14 h-14 text-green-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
            </svg>
          </div>
          <h2 className="text-3xl font-bold">¡Cuenta creada!</h2>
          <p className="text-slate-300 mt-2">Redirigiendo al login...</p>

          <div className="animate-spin mt-6 rounded-full h-10 w-10 border-b-2 border-green-400 mx-auto"></div>
        </div>
      </div>
    );
  }

  // FORMULARIO PRINCIPAL — ESTILO LOGIN ULTRA MODERNO
  return (
    <div className="min-h-screen flex items-center justify-center px-4 
      bg-gradient-to-br from-slate-900 via-slate-800 to-black 
      relative overflow-hidden">

      {/* Luces difusas atrás */}
      <div className="absolute w-[450px] h-[450px] bg-purple-600/20 rounded-full blur-[150px] top-10 left-16 animate-pulse"></div>
      <div className="absolute w-[350px] h-[350px] bg-blue-500/20 rounded-full blur-[140px] bottom-10 right-10 animate-pulse"></div>

      <div className="w-full max-w-3xl relative z-10">
        <div className="rounded-3xl bg-white/10 backdrop-blur-2xl border border-white/10 shadow-2xl overflow-hidden animate-fade-in">

          {/* HEADER */}
          <div className="text-center py-10 px-5 bg-gradient-to-r from-purple-600/60 to-blue-600/60 backdrop-blur-xl">
            <div className="w-24 h-24 mx-auto bg-white rounded-full shadow-lg flex items-center justify-center">
              <FileUser className="w-14 h-14 text-purple-600" />
            </div>
            <h1 className="text-4xl text-white font-extrabold mt-4 drop-shadow-lg">
              Crear Cuenta
            </h1>
            <p className="text-purple-200 mt-1">Únete al Sistema POS moderno</p>
          </div>

          {/* FORM */}
          <div className="p-10 text-white">
            <form onSubmit={handleSubmit} className="space-y-7">

              {/* FILA 1: Usuario / Email */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">

                {/* Usuario */}
                <div className="relative group">
                  <User className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400 w-5 h-5" />
                  <input
                    type="text"
                    name="username"
                    value={formData.username}
                    onChange={handleChange}
                    className={`w-full bg-white/5 border ${errors.username ? 'border-red-500' : 'border-white/10'} rounded-xl
                      pl-10 py-3 placeholder-slate-400 text-white
                      focus:ring-2 focus:ring-purple-500/70 transition`}
                    placeholder="Usuario"
                    disabled={isLoading}
                  />
                  {errors.username && <p className="text-red-400 text-sm mt-1">{errors.username}</p>}
                </div>

                {/* Email */}
                <div className="relative group">
                  <Mail className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400 w-5 h-5" />
                  <input
                    type="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    className={`w-full bg-white/5 border ${errors.email ? 'border-red-500' : 'border-white/10'} rounded-xl
                      pl-10 py-3 placeholder-slate-400 text-white
                      focus:ring-2 focus:ring-purple-500/70 transition`}
                    placeholder="correo@ejemplo.com"
                    disabled={isLoading}
                  />
                  {errors.email && <p className="text-red-400 text-sm mt-1">{errors.email}</p>}
                </div>

              </div>

              {/* FILA 2: Nombre / Apellido */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">

                <div className="relative group">
                  <FileUser className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400 w-5 h-5" />
                  <input
                    type="text"
                    name="nombre"
                    value={formData.nombre}
                    onChange={handleChange}
                    className={`w-full bg-white/5 border ${errors.nombre ? 'border-red-500' : 'border-white/10'} rounded-xl 
                      pl-10 py-3 text-white placeholder-slate-400 focus:ring-2 focus:ring-purple-500/70 transition`}
                    placeholder="Juan"
                  />
                  {errors.nombre && <p className="text-red-400 text-sm mt-1">{errors.nombre}</p>}
                </div>

                <div className="relative group">
                  <FileUser className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400 w-5 h-5" />
                  <input
                    type="text"
                    name="apellido"
                    value={formData.apellido}
                    onChange={handleChange}
                    className={`w-full bg-white/5 border ${errors.apellido ? 'border-red-500' : 'border-white/10'} rounded-xl 
                      pl-10 py-3 text-white placeholder-slate-400 focus:ring-2 focus:ring-purple-500/70 transition`}
                    placeholder="Pérez"
                  />
                  {errors.apellido && <p className="text-red-400 text-sm mt-1">{errors.apellido}</p>}
                </div>

              </div>

              {/* FILA 3: Contraseña / Confirmación */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">

                {/* Contraseña */}
                <div className="relative group">
                  <Shield className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400 w-5 h-5" />
                  <input
                    type={showPass ? 'text' : 'password'}
                    name="password"
                    value={formData.password}
                    onChange={handleChange}
                    className={`w-full bg-white/5 border ${errors.password ? 'border-red-500' : 'border-white/10'} rounded-xl 
                      pl-10 pr-12 py-3 text-white placeholder-slate-400 focus:ring-2 focus:ring-purple-500/70 transition`}
                    placeholder="Contraseña"
                  />

                  <button
                    type="button"
                    onClick={() => setShowPass(prev => !prev)}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-slate-400 hover:text-white transition"
                  >
                    {showPass ? <EyeOff size={20} /> : <Eye size={20} />}
                  </button>

                  {errors.password && <p className="text-red-400 text-sm mt-1">{errors.password}</p>}
                </div>

                {/* Confirmación */}
                <div className="relative group">
                  <Shield className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400 w-5 h-5" />
                  <input
                    type={showConfirmPass ? 'text' : 'password'}
                    name="confirmPassword"
                    value={formData.confirmPassword}
                    onChange={handleChange}
                    className={`w-full bg-white/5 border ${errors.confirmPassword ? 'border-red-500' : 'border-white/10'} rounded-xl 
                      pl-10 pr-12 py-3 text-white placeholder-slate-400 focus:ring-2 focus:ring-purple-500/70 transition`}
                    placeholder="Confirmar contraseña"
                  />

                  <button
                    type="button"
                    onClick={() => setShowConfirmPass(prev => !prev)}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-slate-400 hover:text-white transition"
                  >
                    {showConfirmPass ? <EyeOff size={20} /> : <Eye size={20} />}
                  </button>

                  {errors.confirmPassword && <p className="text-red-400 text-sm mt-1">{errors.confirmPassword}</p>}
                </div>

              </div>

              {/* TÉRMINOS */}
              <div className="flex items-start text-slate-300">
                <input type="checkbox" className="w-4 h-4 mt-1 rounded border-white/20 bg-white/5" required />
                <label className="ml-3 text-sm">
                  Acepto los{" "}
                  <span className="text-purple-300 hover:text-purple-200 underline cursor-pointer">
                    términos y condiciones
                  </span>{" "}
                  y la{" "}
                  <span className="text-purple-300 hover:text-purple-200 underline cursor-pointer">
                    política de privacidad
                  </span>.
                </label>
              </div>

              {/* BOTÓN */}
              <button
                type="submit"
                disabled={isLoading}
                className="w-full py-4 rounded-xl font-bold text-white text-lg
                  bg-gradient-to-r from-purple-600 to-blue-600
                  hover:from-purple-700 hover:to-blue-700
                  shadow-[0_5px_25px_rgba(0,0,0,0.4)]
                  hover:shadow-[0_5px_35px_rgba(0,0,0,0.6)]
                  transition-all duration-300 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center"
              >
                {isLoading ? (
                  <>
                    <div className="animate-spin rounded-full h-5 w-5 border-b-2 mr-3"></div>
                    Creando cuenta...
                  </>
                ) : (
                  <>
                    <FileUser className="w-6 h-6 mr-2" />
                    Crear Cuenta
                  </>
                )}
              </button>

            </form>
          </div>

          {/* FOOTER */}
          <div className="py-6 text-center bg-white/5 border-t border-white/10">
            <p className="text-slate-300">
              ¿Ya tienes una cuenta?{' '}
              <Link href="/login" className="text-purple-300 hover:text-purple-100 underline font-semibold">
                Iniciar sesión
              </Link>
            </p>
          </div>

        </div>
      </div>
    </div>
  );
}
